.container-about {
  margin-top: 100px;

  position: relative;
  #about {
    position: absolute;
    top: -250px;
  }
  .skills {
    background-color: #0060e6;
    color: white;
    border-radius: 10px;
    margin-right: 10px;
    padding: 5px;
    position: relative;
    top: 10px;
    // overflow: hidden !important;
  }
}

@media screen and (max-width: 768px) {
  .container-about {
    #about {
      position: absolute;
      top: -100px;
    }
    .containerSkills {
      margin-top: 100px !important;
      .subContainerSkills {
        display: flex;
        flex-flow: wrap;

        .skills {
          margin-top: 10px;
        }
      }
    }
  }
}
