.card {
  padding: 0px !important;
  margin: 10px 15px !important;

  @media (max-width: 500px) {
    padding: 0px !important;
    margin: 20px 0px !important;
  }
}

.image-gallery-left-nav {
  position: absolute !important;
  // left: 50% !important;

  svg {
    height: 80px !important;
  }
}

.image-gallery-right-nav {
  position: absolute !important;
  // left: 50% !important;

  svg {
    height: 80px !important;
  }
}

.image-gallery-icon .image-gallery-left-nav {
  position: absolute !important;
}

.container-project {
  position: relative !important;
  margin-top: 120px;

  // Fix the margin of Cards
  .row {
    margin-right: 0px !important;
    margin-left: 0px !important;

    @media (max-width: 500px) {
      margin-right: -10px !important;
      margin-left: -10px !important;
    }
  }

  #projects {
    position: absolute;
    top: -40px;
  }

  // כאןןןן
  .containerProjectImage {
    .subContainerProjects {
      @media (max-width: 500px) {
        // padding: 0px 15px;
      }
    }

    .card-body {
      div {
        display: flex !important;
        flex-flow: wrap !important;
        .infoProjectSkills {
          margin-top: 10px;
        }
      }
    }

    .infoProjectSkills {
      background-color: #00c4cc;
      color: white;
      border-radius: 10px;
      margin-right: 10px;

      padding: 5px;
      position: relative;
      top: 10px;
      // overflow: hidden !important;
    }
    img {
      cursor: pointer;
      transition: all ease-in 0.5s;
      &:hover {
        transition: all ease-out 0.5s;
        transform: scale(0.9);
      }
      &:active {
        &:hover {
          transition: all ease-out 0.5s;
          transform: scale(0.9);
        }
      }
    }
  }
}

@media screen and (max-width: 993px) {
  .container-project {
    .containerProjectImage {
      margin-top: 100px;
      &:nth-child(3) {
        margin-top: 100px;
      }
      &:nth-child(4) {
        margin-top: 100px;
      }
      &:nth-child(5) {
        margin-top: 100px;
      }
    }
  }
}

@media screen and (max-width: 502px) {
  .container-project {
    padding: 15px;
    #projects {
      position: absolute;
      top: -60px;
      // bottom: 150px;
    }
  }
}
