.containerContactTitle {
  text-align: center;
  margin-top: 100px;
  margin-bottom: 100px;
  position: relative;
}

.containerContact {
  // background-color: #6fd448;
  background-color: orangered;
  padding: 3rem 0;

  .contactIcons {
    // text-align: center;
    // color: green;
    font-size: 30px !important;
  }

  .subContainerContact {
    display: grid;
    grid-template-columns: repeat(3, 1fr);

    text-align: center;
  }
}

@media screen and (max-width: 506px) {
  #contact {
    position: absolute;
    bottom: 150px;
  }
  .containerContact {
    .subContainerContact {
      grid-template-columns: 1fr !important;
      padding: 0 !important;
      margin: 0 !important;
      //   text-align: center;
      .borderBotTitle {
        padding-bottom: 20px;
        &:nth-child(1) {
          border-bottom: 1px solid white;
        }
        &:nth-child(2) {
          border-bottom: 1px solid white;
        }
      }
    }
  }
}
